import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TableFooter from "@mui/material/TableFooter";

const dispatchedEvents = [];

const SrsMuiTableFooter = function (props, ref) {
  return (
    <TableFooter {...props} theme={props.muiTheme}>
      <slot />
    </TableFooter>
  );
}

const styles = `
  /* Custom styles for the MuiTableFooter component */
`;

const props = [];

const slots = [
  {
    name: "default",
    allowed: ["srs-mui-table-row"],
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiTableFooter), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
